import React from 'react';
import { Form } from 'react-bootstrap';
import { useTranslation } from 'react-i18next';

const CouleurField = ({ 
  selectedCategory,
  selectedSubCategory,
  postData, 
  handleChangeInput, 
  isRTL, 
  name = 'couleur', 
  label = 'color'
}) => {
  const { t } = useTranslation('camposcomunes');
  
  // ğŸ“¦ BASE DE DATOS DE COLORES DINÃMICA POR CATEGORÃA Y SUBCATEGORÃA
  const allColorsByCategory = {
    // UNIVERSAL: colores disponibles para cualquier categorÃ­a
    'universal': [
      'Noir', 'Blanc', 'Rouge', 'Bleu', 'Vert', 'Jaune', 'Gris', 
      'Marron', 'Orange', 'Violet', 'Rose', 'Beige', 'Argent', 'Or'
    ],
    
    // ğŸ“± TÃ‰LÃ‰PHONES & Ã‰LECTRONIQUE
    'telephones': {
      'smartphones': [ 
        'Noir', 'Blanc', 'Gris SidÃ©ral', 'Bleu Minuit', 'Vert Alpin', 
        'Rouge Produit', 'Or Rose', 'Violet', 'Bleu Pacific', 'Vert Menthe'
      ],
      'telephones_cellulaires': [ 
        'Noir', 'Blanc', 'Gris SidÃ©ral', 'Bleu Minuit', 'Vert Alpin', 
        'Rouge Produit', 'Or Rose', 'Violet', 'Bleu Pacific', 'Vert Menthe'
      ],


      'tablettes': [
        'Gris SidÃ©ral', 'Argent', 'Or', 'Noir', 'Blanc', 'Bleu', 'Rose Gold'
      ],
      'accessoires': [
        'Noir', 'Transparent', 'Bleu', 'Rouge', 'Vert', 'Rose', 'Violet', 'Multicolore'
      ],
      'default': ['Noir', 'Blanc', 'Gris', 'Or Rose', 'Bleu', 'Rouge']
    },
    
    // ğŸ’» INFORMATIQUE
    'informatique': {
      'ordinateurs_portables': [
        'Gris SidÃ©ral', 'Argent', 'Noir Mat', 'Bleu Minuit', 'Bordeaux', 
        'Vert FoncÃ©', 'Blanc NacrÃ©', 'Rouge Gaming'
      ],
      'souris_claviers': [
        'Noir', 'Blanc', 'Rouge', 'Bleu', 'Vert', 'RGB', 'Noir/Argent', 'Blanc/Rose'
      ],
      'default': ['Noir', 'Gris', 'Argent', 'Blanc', 'Bleu']
    },
    
    // ğŸ›‹ï¸ MEUBLES & DÃ‰CORATION
    'meubles': {
      'canapes': [
        'Gris', 'Beige', 'Marron', 'Noir', 'Bleu FoncÃ©', 'Vert FoncÃ©', 'Bordeaux', 'Taupe'
      ],
      'lits': [
        'Blanc', 'Noir', 'ChÃªne Naturel', 'ChÃªne FoncÃ©', 'Noyer', 'ChÃªne Blanchi', 'Gris Bois'
      ],
      'tables': [
        'ChÃªne', 'Noyer', 'Verre Transparent', 'Verre FumÃ©', 'Marbre Blanc', 'Noir LaquÃ©'
      ],
      'default': ['ChÃªne', 'Blanc', 'Noir', 'Gris', 'Marron', 'Beige']
    },
    
    // ğŸ‘• VÃŠTEMENTS & MODE
    'vetements': {
      'vetements_homme': [
        'Noir', 'Bleu Marine', 'Gris', 'Marron', 'Beige', 'Blanc', 'Bordeaux', 'Vert FoncÃ©'
      ],
      'vetements_femme': [
        'Noir', 'Blanc', 'Rose', 'Rouge', 'Bleu Clair', 'Violet', 'Vert Menthe', 'Corail'
      ],
      'chaussures': [
        'Noir', 'Brun', 'Blanc', 'Beige', 'Bleu Marine', 'Gris', 'Rouge', 'Vert'
      ],
      'default': ['Noir', 'Blanc', 'Bleu', 'Rouge', 'Gris']
    },
    
    // ğŸš— AUTOMOBILES
    'automobiles': {
      'voitures': [
        'Noir NacrÃ©', 'Blanc Glacier', 'Gris MÃ©tallisÃ©', 'Bleu Marine', 'Rouge Ferrari',
        'Argent MÃ©tallisÃ©', 'Vert FoncÃ©', 'Marron', 'Bleu Nuit', 'Gris Anthracite'
      ],
      'motos': [
        'Noir Mat', 'Rouge Racing', 'Bleu Royal', 'Jaune', 'Vert Lime', 'Orange', 'Blanc/Noir'
      ],
      'default': ['Noir', 'Blanc', 'Gris', 'Argent', 'Rouge', 'Bleu']
    },
    
    // ğŸ  Ã‰LECTROMÃ‰NAGER
    'electromenager': {
      'televiseurs': ['Noir', 'Argent', 'Gris FumÃ©', 'Blanc', 'Or Rose', 'Bord de Mer'],
      'refrigerateurs': ['Blanc', 'Inox', 'Noir Inox', 'Gris FoncÃ©', 'Noir Mat'],
      'machines_laver': ['Blanc', 'Inox', 'Gris', 'Noir', 'Argent'],
      'default': ['Blanc', 'Inox', 'Noir', 'Gris']
    },
    
    // ğŸ’ BIJOUX & MONTRES
    'bijoux': {
      'montres': ['Argent', 'Or Jaune', 'Or Rose', 'Or Blanc', 'Acier', 'Noir', 'Bicolor'],
      'colliers': ['Or', 'Argent', 'Acier', 'Cuivre', 'Laiton', 'Noir', 'DorÃ©'],
      'bagues': ['Or Jaune', 'Or Rose', 'Argent', 'Platine', 'Acier', 'Or Blanc'],
      'default': ['Or', 'Argent', 'Acier', 'DorÃ©']
    },
    
    // âœˆï¸ VOYAGES & BAGAGES
    'voyages': {
      'valises': ['Noir', 'Bleu Marine', 'Gris', 'Rouge', 'Vert', 'Bordeaux', 'Noir/Argent'],
      'sacs_voyage': ['Noir', 'Marron', 'Beige', 'Vert FoncÃ©', 'Bleu', 'Gris', 'Camouflage'],
      'default': ['Noir', 'Bleu', 'Gris', 'Rouge']
    }
  };

  // ğŸ” FUNCIÃ“N PARA OBTENER COLORES SEGÃšN SELECCIÃ“N
  const getFilteredColors = () => {
    // Si no hay categorÃ­a, devolver colores universales
    if (!selectedCategory) {
      return allColorsByCategory['universal'];
    }
    
    const categoryColors = allColorsByCategory[selectedCategory];
    
    // Si la categorÃ­a no tiene colores especÃ­ficos, devolver universales
    if (!categoryColors) {
      return allColorsByCategory['universal'];
    }
    
    // Si la categorÃ­a tiene subcategorÃ­as y hay una seleccionada
    if (selectedSubCategory && categoryColors[selectedSubCategory]) {
      return categoryColors[selectedSubCategory];
    }
    
    // Si hay colores por defecto para la categorÃ­a
    if (categoryColors.default) {
      return categoryColors.default;
    }
    
    // Ãšltimo recurso: colores universales
    return allColorsByCategory['universal'];
  };

  const colors = getFilteredColors();
  
  return (
    <Form.Group>
      <Form.Label>ğŸ¨ {t(label)}</Form.Label>
      <Form.Select
        name={name}
        value={postData[name] || ''}
        onChange={handleChangeInput}
        dir={isRTL ? 'rtl' : 'ltr'}
      >
        <option value="">{t('select_color')}</option>
        
        {/* Agrupar colores si hay muchos */}
        {colors.length > 10 ? (
          <>
            <optgroup label={t('couleurs_principales')}>
              {colors.slice(0, 8).map(color => (
                <option key={color} value={color.toLowerCase()}>
                  {color}
                </option>
              ))}
            </optgroup>
            <optgroup label={t('toutes_couleurs')}>
              {colors.slice(8).map(color => (
                <option key={color} value={color.toLowerCase()}>
                  {color}
                </option>
              ))}
            </optgroup>
          </>
        ) : (
          colors.map(color => (
            <option key={color} value={color.toLowerCase()}>
              {color}
            </option>
          ))
        )}
        
        <option value="autre">{t('autre')}</option>
      </Form.Select>
      
      {/* Campo para color personalizado */}
      {postData[name] === 'autre' && (
        <Form.Control
          type="text"
          name={`${name}_custom`}
          value={postData[`${name}_custom`] || ''}
          onChange={handleChangeInput}
          placeholder={t('precisez_couleur')}
          className="mt-2"
          dir={isRTL ? 'rtl' : 'ltr'}
        />
      )}
      
      {/* InformaciÃ³n contextual (mantenemos texto original) */}
      {selectedCategory && (
        <Form.Text className="text-muted">
          <small>
            {colors.length} couleur(s) disponible(s) pour 
            <strong> {selectedCategory}</strong>
            {selectedSubCategory && ` > ${selectedSubCategory}`}
          </small>
        </Form.Text>
      )}
    </Form.Group>
  );
};

export default CouleurField;